.song-container{
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    align-items:  center;
    justify-content: center;

  
    h2{
        padding: 3rem 1rem 1rem 1rem;
    }
    h3{
        font-size: 1rem;
    }

   img{
       width: 20%;
       border-radius: 50%; 
       
    } 


    .rotate{
        animation: rotate 5s infinite linear; 
    }

    .rotateStop{
        animation: rotate 5s infinite linear; 
        animation-play-state: paused;
    }
 

    @keyframes rotate {
        100%   {transform: rotate(360deg)}
      }

  
    }

    @media screend and (max-width:768px){
        img{
            width:60%;
        }
    }
